import { useState, useEffect } from "react";
import "./AddUser.css";
import { useHistory } from 'react-router-dom';
import firebase from "../../util/firebase";

export default function AddUser() {

    const [user, setUser] = useState([]);
    
    const { first_name, last_name, email_addr, age } = user;

    let history = useHistory();

    useEffect(() => {
        setUser({
            first_name: '',
            last_name: '',
            email_addr: '',
            age: ''
        });
    },[])

    
    
    const onSubmit = async (e) => {
        e.preventDefault();

        // history.push("/add-user");

        const userRef = firebase.database().ref("users");

        userRef.push(user);

        e.target[0].value = ''; // For First_Name
        e.target[1].value = ''; // For Last_Name
        e.target[2].value = ''; // For Email_Address
        e.target[3].value = ''; // For Age

        console.log(e.target.value);
    }
    
    const onInputChange= e => {
        setUser({...user, [e.target.name]: e.target.value });
    }

    return (
        <div className="editUser">
            <div className="editUserFormWrapper">
                <div className="h3 mb-3">Edit A User</div>
                <form onSubmit={e => onSubmit(e)}>
                    <div className="form-group mb-2">
                        <label htmlFor="">First Name</label>
                        <input type="text"
                            className="form-control"
                            placeholder="First Name"
                            name="first_name"
                            value={first_name}
                            onChange={e=>onInputChange(e)}
                        />
                    </div>
                    <div className="form-group mb-2">
                        <label htmlFor="">Last Name</label>
                        <input type="text"
                            className="form-control"
                            placeholder="Last Name"
                            name="last_name"
                            value={last_name}
                            onChange={e=>onInputChange(e)}
                        />
                    </div>
                    <div className="form-group mb-2">
                        <label htmlFor="">Email Address</label>
                        <input type="text"
                            className="form-control"
                            placeholder="Email Address"
                            name="email_addr"
                            value={email_addr}
                            onChange={e=>onInputChange(e)}
                        />
                    </div>
                    <div className="form-group mb-2">
                        <label htmlFor="">Age</label>
                        <input type="text"
                            className="form-control"
                            placeholder="Age"
                            name="age"
                            value={age}
                            onChange={e=>onInputChange(e)}
                        />
                    </div>
                    <div className="form-button">
                        <button className="btn btn-primary">Save</button>
                    </div>
                </form>
            </div>
        </div>
    )
}
