import "./Sidebar.css"
import { AttachMoney, ChatBubbleOutline, DynamicFeed, LineStyle, MailOutline, PermIdentity, Report, Storefront, Timeline, TrendingUp, WorkOutline } from '@material-ui/icons';
import { Link } from "react-router-dom";

export default function Sidebar() {
    return (
        <div className="sidebar">
            <div className="sidebarWrapper">
                <div className="sidebarMenu">
                    <h6 className="sidebarTile">Dashboard</h6>
                    <ul className="sidebarList">
                        <li className="sidebarListItem">
                            <Link to="/">
                                <LineStyle />
                                Home
                            </Link>
                        </li>
                        <li className="sidebarListItem">
                            <Timeline />
                            Analytics
                        </li>
                        <li className="sidebarListItem">
                            <TrendingUp />
                            Sales
                        </li>
                    </ul>
                </div>
                <div className="sidebarMenu">
                    <h6 className="sidebarTile">Quick Menu</h6>
                    <ul className="sidebarList">
                        <li className="sidebarListItem">
                            <Link to="/users">
                                <PermIdentity />
                                Users
                            </Link>
                        </li>
                        <li className="sidebarListItem">
                            <Storefront />
                            Products
                        </li>
                        <li className="sidebarListItem">
                            <AttachMoney />
                            Transactions
                        </li>
                        <li className="sidebarListItem">
                            <TrendingUp />
                            Reports
                        </li>
                    </ul>
                </div>
                <div className="sidebarMenu">
                    <h6 className="sidebarTile">Notification</h6>
                    <ul className="sidebarList">
                        <li className="sidebarListItem">
                            <MailOutline />
                            Mail
                        </li>
                        <li className="sidebarListItem">
                            <DynamicFeed />
                            Feedback
                        </li>
                        <li className="sidebarListItem">
                            <ChatBubbleOutline />
                            Messages
                        </li>
                    </ul>
                </div>
                <div className="sidebarMenu">
                    <h6 className="sidebarTile">Staff</h6>
                    <ul className="sidebarList">
                        <li className="sidebarListItem">
                            <WorkOutline />
                            Manage
                        </li>
                        <li className="sidebarListItem">
                            <Timeline />
                            Analytics
                        </li>
                        <li className="sidebarListItem">
                            <Report />
                            Reports
                        </li>
                    </ul>
                </div>
            </div>
        </div>
    )
}
